body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

hr {
    border: 1px solid #008dd7 !important;
}

.main {
    width: 75vw !important;
    background: #dbe5f1;
    padding: 2.5rem 0;

}

@media (max-width: 600px) {
    .main {
        width: 100vw !important;
    }
    .title {
        text-align: center !important;
        margin: auto !important;
    }
}

.section-light {
    background: #dbe5f1;
    margin: 0.6rem 0 0.6rem 0;
    padding: 0.6rem 0 0.6rem 0;
    font-family: "Quicksand";
}

.section-dark {
    background: #365f91;
    color: white;
    margin: 0.6rem 0 0.6rem 0;
    padding: 0.6rem 0 0.6rem 0;
    font-family: "Quicksand";
}

.name {
    color: #365f91;
    font-family: "Open Sans";
    font-weight: 900;
    font-size: 4rem;
    text-align: center;
}

.title {
    color: #008dd7;
    font-family: "Courier New";
    font-weight: 900;
    font-size: 1rem;
    text-align: right;
    margin-right: 3.6rem;
}

.section-tag {
    background: #008dd7;
    width: 15.7rem;
    height: 3.2rem;
    margin-bottom: 1rem;
    border-radius: 0.6rem;
}

.section-tag h3 {
    font-family: "Baloo Chettan";
    text-align: center;
    line-height: 3.2rem;
    color: #d8e2f1;
}

.toggle-language-button {
    float: right;
    background-size: cover;
    width: 2rem;
    height: 1.5rem;
    border: solid 0.07rem black;
    border-radius: 0.4rem;
    margin-right: 0.5rem;
}

.footer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    font-style: italic;
}

.info-field-icon-container {
    padding: 0 !important;
    max-width: 1.5rem !important;
}

.info-field-icon-container > img {
    width: 100%;
    padding-right: 0 !important;
}

.info-field-icon-value {
    line-height: 1.5rem;
    padding: 0 !important;
    margin-left: 0.5rem;
}

.info-field-icon-value a {
    text-decoration: none;
}

.point {
    width: 1.25rem;
    height: 1.25rem;
    background: #a4a4a4;
    border-radius: 50%;
    display: inline-block;
    margin: 0.1rem;
}

.point-half {
    background: linear-gradient(to right,  #f3cf41 50%, #a4a4a4 0%);
}

.point-full {
    background: #f3cf41;
}

@media (max-width: 600px) {
    .skill-name {
        text-align: left;
    }
    .skill-container {
        margin-bottom: 1.5rem;
    }
}

@media (min-width: 600px) {
    .skill-name {
        text-align: right;
    }
    .skill-points-bar {
        padding: 0 !important;;
        margin-left: -0.3rem;
    }
    .skill-description {
        padding: 0 !important;
        margin-left: -2rem;
    }
}


.project-avatar {
    float: left;
}
.project-avatar:hover {
}

.project-logo-container {
    max-width: 6rem;
    margin: auto;
}
.project-logo-container > img {
    width: 100%;
    border-radius: 0.5rem;

}

.modal-dialog-custom .modal-content {
    background: #dbe5f1;
}
.modal-header-custom {
    border-bottom: 1px solid #008dd7 !important;
}
.modal-title {
    width: 100%;
}
.modal-title p {
    margin-bottom: 0;
}

.project-about p {
    margin: 0;
}
.stack-logo-container {
    background: white;
    width: 90%;
    border: solid 0.05em #ccc;
    border-radius: 15%;
    margin: 5%;
    float: left;
}
.stack-logo-container > img {
    width: 100%;
    border-radius: 15%;

}


.avatar-container {
    max-width: 14rem;
    margin: auto;
    padding: 0;
}

.avatar-container > img {
    width: 100%;
}

